import React from 'react';
import { hot } from 'react-hot-loader';
import './ButtonPrimary.scss';



function ButtonPrimary({ tag, color, to, label, size, type, icon, onClick }) {
  if (tag === "a")
    return (
      <div className="l-primary-button">
        <a href="/"
          className={
            "btn l-primary-button__button" +
            ' btn-color' +
            ' btn-md'
          }
        //:className="['btn-' + color, 'btn-' + size]"
        >
          <span>{label}</span>
        </a>
      </div>
    );

  if (tag === "button")
    return (
      <div className="l-primary-button">
        <button
          className={
            "btn l-primary-button__button" +
            ' btn-color' +
            ' btn-md' + // ?md
            ' btn-type-base' + //?base
            ' btn-icon-out'
          }
          //:className="['btn-' + color, 'btn-' + size, 'btn-type-' + type, 'btn-' + icon]"
          onClick={onClick}
        >
          <span>{label}</span>
        </button>
      </div>
    );

  return null;
};

export default hot(module)(ButtonPrimary);
