import React from 'react';
import { hot } from 'react-hot-loader';
import { Switch, Route } from 'react-router-dom';
import Loadable from 'react-loadable';
import { routerPaths } from './helper';
import ErrorBoundary from '../components/ErrorBoundary';



const delay = 200;

const HomePage = Loadable({
  loader: () => import('../pages/HomePage'),
  loading: () => null,
  delay,
});
const HowToBuyPage = Loadable({
  loader: () => import('../pages/HowToBuyPage'),
  loading: () => null,
  delay,
});
const TermsPage = Loadable({
  loader: () => import('../pages/TermsPage'),
  loading: () => null,
  delay,
});

const NotFoundPage = Loadable({
  loader: () => import('../pages/NotFoundPage'),
  loading: () => null,
  delay,
});


function AppRouter() {
  return (
    <ErrorBoundary>
      <Switch>
        <Route exact path={routerPaths.main} component={HomePage} />
        <Route exact path={routerPaths.howToBuy} component={HowToBuyPage} />
        <Route exact path={routerPaths.terms} component={TermsPage} />

        <Route component={NotFoundPage} />
      </Switch>
    </ErrorBoundary>
  );
}

export default hot(module)(AppRouter);
