import React, { useState, useEffect } from 'react';
import { hot } from 'react-hot-loader';

import IncrementIcon from './SvgRobo/IncrementIcon';
import DecrementIcon from './SvgRobo/DecrementIcon';
import CloseIcon from './SvgRobo/CloseIcon';

import ButtonPrimary from './ButtonPrimary';

import rPopupImg from '../assets/images/popup/r-popup.png';
import rPopupX2Img from '../assets/images/popup/r-popup@2x.png';
import './MintModal.scss';



function MintModal({ isOpen, closeAction }) {
  const maxCountLimit = 10;
  const maxTokens = 10000;
  const [mintedTokens, setMintedTokens] = useState(0);

  const [count, setCount] = useState(1);

  const onChange = (e) => {
    let newCount = parseInt(e.target.value);
    if (isNaN(newCount)) newCount = 0;
    if (newCount < 0) newCount = 0;
    if (newCount > maxCountLimit) newCount = maxCountLimit;
    setCount(newCount);
  }
  const increment = () => {
    let newCount = count + 1;
    if (newCount > maxCountLimit) newCount = maxCountLimit;
    setCount(newCount);
  }
  const decrement = () => {
    let newCount = count - 1;
    if (newCount < 0) newCount = 0;
    setCount(newCount);
  }

  const [totalEth, setTotalEth] = useState('0.00');
  const [priceEth, setPriceEth] = useState('0.10');
  useEffect(() => {
    setTotalEth((count * priceEth).toFixed(2));
  }, [count, priceEth]);

  if (!isOpen) return null;

  const closeModal = () => {
    closeAction(false);
    setCount(1);
  }

  const mint = () => {
    console.log('mint');
  }

  return (
    <div className="modal-mask">
      <div className="modal-wrapper">
        <div className="modal-overlay" onClick={closeModal}></div>
        <div className="modal-container">

          <div className="modal-img">
            <img src={rPopupImg} srcSet={`${rPopupX2Img} 2x`} width="432" height="432" alt="" title="" />
          </div>

          <div className="modal-body">
            <div className="modal-header"><span>{mintedTokens}</span> / {maxTokens}</div>
            <div className="modal-desc">Roborovski minted</div>

            <div className="modal-select">
              <div className="sel">
                <div className="sel-item" onClick={decrement} ><DecrementIcon /></div>
                <input
                  type="number"
                  value={count}
                  onChange={onChange}
                />
                <div className="sel-item" onClick={increment}><IncrementIcon /></div>
              </div>
              <div className="sel-desc">Amount max {maxCountLimit} per transaction/address</div>

            </div>
            <div className="sel__res">
              <div className="sel__res-col sel__res-col--1">
                <span>{count} x {priceEth} ETH</span>
                <div>Excluding gas fee</div>
              </div>
              <div className="sel__res-col sel__res-col--2">
                <span>{totalEth} ETH</span>
                <div>Total</div>
              </div>
            </div>
            <div className="modal-submit">
              <ButtonPrimary
                tag="button"
                label="Mint"
                size="full"
                onClick={mint}
              />
            </div>
          </div>

          <button className="modal-close-button" onClick={closeModal}><CloseIcon /></button>
        </div>
      </div>
    </div>
  );
};

export default hot(module)(MintModal);
