import { useState, useEffect } from 'react';

import Modal, { useModalState, useModal } from 'react-simple-modal-provider';
import MintModalOld from "./MintModalOld";


//
const MintModal = ({ children }) => {
  const [isOpen, setOpen] = useModalState();
  const onClose = () => {
    setOpen(false);
  }

  useEffect(() => {
    window.ym(87029883, 'reachGoal', 'OpenMintModal');
  }, []);

  //const { open: modal2Open } = useModal('Modal2');
  //const { open: modal3Open } = useModal('Modal3');

  const [data, setData] = useState(null);

  const asyncOpen = async () => {
    try {
      await new Promise((r) => setTimeout(r, 10));
      const res = await fetch('https://jsonplaceholder.typicode.com/todos/1');
      const json = await res.json();
      setData(json);
    } catch (e) {
      console.log(e);
    }
  };

  //<MintModalOld isOpen={true} closeAction={() => null} />
  return (
    <Modal
      id={'MintModal'}
      consumer={children}
      isOpen={isOpen}
      setOpen={setOpen}
      asyncOpen={asyncOpen}
      draggable={false}
      duration={300}
    //animation={modalAnimation.scaleUp}
    //width={300}
    //height={300}
    //radius={10}
    //backgroundColor={'#fff'}
    >
      <MintModalOld isOpen={true} closeAction={() => setOpen(false)} />
    </Modal >
  );
};

export default MintModal;
